import { Routes, RouterModule } from '@angular/router';

import { ForbiddenComponent } from './shared/components/forbidden/forbidden.component';
import { UnauthorizedComponent } from './shared/components/unauthorized/unauthorized.component';
import { CanActivateGuard } from './shared/services/guard.service';


const routes: Routes = [
    { path: '', redirectTo: 'home', pathMatch: 'full' },
    {
        path: 'appointments',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\+appointments\\appointments.module')['AppointmentsModule']);  });})
    },
    {
        path: 'pets',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\+pets\\pets.module')['PetsModule']);  });})
    },
    {
        path: 'calendars',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\+calendars\\calendars.module')['CalendarsModule']);  });})
    },
    { path: 'forbidden', component: ForbiddenComponent },
    { path: 'unauthorized', component: UnauthorizedComponent }
];

export const InternalRouterModule = RouterModule.forRoot(routes);



